import { isIOSStatusBar59 } from './isIOSStatueBar59';
import { isIOSStatusBar47 } from './isIOSStatusBar47';
import { isIOSStatusBar48 } from './isIOSStatusBar48';
import { isIOSStatusBar50 } from './isIOSStatusBar50';
import { isIphoneX } from './isIphoneX';

/**
 * 获取iPhone的statusBar高度
 * @returns iphone对应的statusBar高度
 */
export function getIPhoneStatusBarHeight() {
    if (isIphoneX()) {
        if (isIOSStatusBar47()) {
            return 47;
        }
        if (isIOSStatusBar48()) {
            return 48;
        }
        if (isIOSStatusBar50()) {
            return 50;
        }
        if (isIOSStatusBar59()) {
            return 59;
        }
        return 44;
    }
    return 20;
}
